<template>
  <modal :visible="visible" @close="close">
    <div v-if="stage">
      <h5>:</h5>
      <input name="title" :value="stage.title" @change="fieldChange" />

      <h5>
         :
      </h5>
      <select name="net_size" :value="stage.net_size" @change="fieldChange">
        <option>16</option>
        <option>32</option>
        <option>64</option>
        <option>128</option>
        <option>256</option>
      </select>
      <h5>
         :
      </h5>
      <select name="net_type" :value="stage.net_type" @change="fieldChange">
        <option>2-0</option>
        <option>2-1</option>
      </select>
      <br />
      <h5>:</h5>
      <datepicker
        :value="stage.start_date"
        @change="changeStageStart"
        :with-time="true"
      />
      <h5>:</h5>
      <datepicker
        :value="stage.end_date"
        @change="changeStageEnd"
        :with-time="true"
      />
      <div>
        <button @click="close"></button>
        <button @click="submitNewStage"></button>
      </div>
    </div>
  </modal>
</template>

<script>
  import Modal from '../Modal.vue';
  import Datepicker from '../Datepicker.vue';

  export default {
    name: 'stage-creation-form',
    components: {
      Modal,
      Datepicker
    },
    props: [
      'visible',
      'stage'
    ],
    methods: {
      close() {
        this.$emit('close');
      },
      fieldChange(e) {
        this.$emit('change', {
          [e.target.name]: e.target.value
        });
      },
      changeStageStart(value) {
        this.$emit('change', {
          start_date: value
        });
      },
      changeStageEnd(value) {
        this.$emit('change', {
          end_date: value
        });
      },
      submitNewStage() {
        this.$emit('save');
      }
    }
  }
</script>

<style scoped>

</style>



