<template>
  <modal :visible="visible" @close="close">
    <div v-if="tournament">
      <h5>:</h5>
      <input name="title" :value="tournament.title" @change="fieldChange" />
      <h5>
        :
      </h5>
      <textarea name="description" :value="tournament.description" @change="fieldChange"></textarea>
      <h5>
        :
      </h5>
      <label>
        <input type="checkbox" name="is_rating" :value="tournament.is_rating" @change="fieldChange" /> 
      </label>

      <br />
      <h5>:</h5>
      <datepicker
        :value="tournament.start_date"
        @change="changeTournamentStart"
      />
      <h5>:</h5>
      <datepicker
        :value="tournament.end_date"
        @change="changeTournamentEnd"
      />
      <div>
        <button @click="close"></button>
        <button @click="submitNewTournament"></button>
      </div>
    </div>
  </modal>
</template>

<script>
  import Modal from '../Modal.vue';
  import Datepicker from '../Datepicker.vue';

  export default {
    name: 'tournament-creation-form',
    components: {
      Modal,
      Datepicker
    },
    props: [
      'visible',
      'tournament'
    ],
    methods: {
      close() {
        this.$emit('close');
      },
      fieldChange(e) {
        this.$emit('change', {
          [e.target.name]: e.target.value
        });
      },
      changeTournamentStart(value) {
        this.$emit('change', {
          start_date: value
        });
      },
      changeTournamentEnd(value) {
        this.$emit('change', {
          end_date: value
        });
      },
      submitNewTournament() {
        this.$emit('save');
      }
    }
  }
</script>

<style scoped>

</style>



