<template>
  <div id="app">
    <nav class="nav">
      <router-link to="/">
        
      </router-link>

      <router-link to="/tournaments">
        
      </router-link>

      <router-link to="/players">
        
      </router-link>

      <router-link to="/service">
        
      </router-link>
    </nav>
    <router-view></router-view>
    <div class="gag" v-if="loading" @click.stop></div>
  </div>
</template>

<script>
  export default {
    name: 'App',
    mounted: function() {
      this.$store.dispatch('getTournaments');
    },
    computed: {
      isOnMainPage: function() {
        return this.$route.name === 'main';
      },
      loading: function() {
        return this.$store.state.interface.loading;
      }
    }
  }
</script>

<style scoped>
  .nav {
    display: flex;
    gap: 30px;
  }

  .router-link-exact-active {
    color: black;
  }

  .gag {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.4);
    z-index: 10000;
  }
</style>



