import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import Main from './Main.vue';
import Service from './Service.vue';
import Tournaments from './Tournaments.vue';

const routes = [
  {
    path: '/',
    name: 'main',
    component: Main
  },
  {
    path: '/tournaments',
    name: 'tournaments',
    component: Tournaments
  },
  {
    path: '/players',
    name: 'players',
    component: Main
  },
  {
    path: '/service',
    name: 'service',
    component: Service
  }
];

export default new VueRouter({
  routes
});



