<template>
  <div v-if="stage">
    <div class="playersListsWrap">
      <div v-if="registeredUsers.length !== seeds">
        <h3> :</h3>
        <suggest
          v-if="!isFull"
          :items="suggestions"
          @input="findUser"
          placeholder="   "
          @pick="pickUser"
        />

        <ol>
          <li v-for="user in pickedUsers">
            <span class="userName">{{user.name}}</span>
            <button @click.stop="removeUser(user)">x</button>
          </li>
        </ol>

        <button @click="saveChanges">    </button>
      </div>
      <div>
        <h3 v-if="!registeredUsers.length">  </h3>
        <h3 v-if="registeredUsers.length"> :</h3>

        <table v-if="registeredUsers.length" class="playersTable">
          <thead>
            <tr>
              <td> </td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(user, index) in registeredUsers">
              <td>{{index}}</td>
              <td>{{user.name}}</td>
              <td><input type="number" :value="user.handicap" :data-user="user.id" @change="setUserHandicap"></td>
              <td><button @click.stop="unregisterUser(user.id)">x</button></td>
            </tr>
          </tbody>
        </table>

        <span> <strong>{{placesLeft}}</strong> </span>
      </div>
    </div>
    <button @click="finishRegistration"> !</button>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import api from './api';

  import Suggest from './Suggest.vue';

  export default {
    name: 'new-stage',
    components: {
      Suggest
    },
    data: function () {
      return {
        suggestions: [],
        inputValue: '',
        pickedUsers: []
      };
    },
    computed: {
      ...mapState({
        stage: state => state.interface.selectedStage
      }),
      seeds: function() {
        return Number(this.stage.net_size);
      },
      registeredUsers: function() {
        return this.stage.registrations || [];
      },
      placesLeft: function() {
        return this.seeds - this.registeredUsers.length - this.pickedUsers.length;
      },
      isFull: function() {
        return !this.placesLeft;
      }
    },
    methods: {
      updateStage(data) {
        this.$store.commit('editSelectedStage', data);
      },
      updateRegisteredUsers(rawData) {
        this.updateStage({
          registrations: Array.isArray(rawData) ? rawData : Object.values(rawData)
        });
      },
      findUser(e) {
        this.inputValue = e.target.value.trim();

        if (!this.inputValue) {
          return;
        }

        api.suggestUser(e.target.value)
          .then(d => {
            this.suggestions = Object.values(d).map(i => ({...i, display: i.name}));
          });
      },
      pickUser(user) {
        this.suggestions = [];
        if (
          !this.pickedUsers.find(pickedUser => user.id === pickedUser.id) &&
          !this.registeredUsers.find(pickedUser => user.id === pickedUser.id)
        ) {
          this.pickedUsers = this.pickedUsers.concat(user);
        }
      },
      removeUser(user) {
        this.pickedUsers = this.pickedUsers.filter(u => u.id !== user.id);
      },
      unregisterUser(userId) {
        this.$store.commit('startLoading');
        return api.unregisterPlayerFromStage(userId, this.stage.id)
          .then(users => {
            this.$store.commit('stopLoading');
            this.updateRegisteredUsers(users);
          });
      },
      setUserHandicap(event) {
        if (!event.target.value.match(/^\d$/)) {
          return;
        }

        this.$store.commit('startLoading');
        api.setPlayerStageHandicap({
          player_id: event.target.dataset.user,
          stage_id: this.stage.id,
          tournament_id: this.stage.tournament_id,
          value: event.target.value
        })
          .then(users => {
            this.$store.commit('stopLoading');
            this.updateRegisteredUsers(users);
          });
      },
      saveChanges() {
        this.$store.commit('startLoading');
        api.submitTournamentRegistrationUsers(this.pickedUsers, this.stage.id)
          .then(users => {
            this.$store.commit('stopLoading');
              this.updateRegisteredUsers(users);
              this.pickedUsers = [];
          });
      },
      finishRegistration() {
        if (confirm(' ?   ,     !')) {
          this.$store.commit('startLoading');
          api.closeRegistration(this.stage.id)
            .then(() => {
              this.$store.commit('stopLoading');
              this.$store.dispatch('selectStage', this.stage.id)
            });
        }
      }
    }
  }
</script>

<style scoped>
  .playersListsWrap {
    display: flex;
    gap: 30px;
  }

  .playersTable td {
    border: 1px gray solid;
    padding: .2rem;
  }

  .userName {
    margin-right: 20px;
  }
</style>



