<template>
  <div class="match" @click="openModal">
    <div
      :class="[
              'player-row',
              hoveredPlayer === game.pl1_id ? 'hovered-player' : '',
              getCN(true)
            ]"
      :data-player="game.pl1_id"
      @mouseenter="hoverPlayer(game.pl1_id)"
      @mouseleave="unhoverPlayer"
    >
      <span class="player-name">{{game.user1}} <span v-if="game.user1">({{game.user1_handicap}})</span></span>
      <span class="score">{{game.pl1_score}}</span>
    </div>
    <div
      :class="[
                'player-row',
                hoveredPlayer === game.pl2_id ? 'hovered-player' : '',
                getCN(false)
              ]"
      :data-player="game.pl2_id"
      @mouseenter="hoverPlayer(game.pl2_id)"
      @mouseleave="unhoverPlayer"
    >
      <span class="player-name">{{game.user2}} <span v-if="game.user2">({{game.user2_handicap}})</span></span>
      <span class="score">{{game.pl2_score}}</span>
    </div>
    <modal :visible="editingMatch" @close="closeModal">
      <div v-if="editingMatch" class="modal-content">
        <label>
          <h5>{{_game.user1}}</h5>
          <input v-model="_game.pl1_score" type="number" />
        </label>
        <label>
          <h5>{{_game.user2}}</h5>
          <input v-model="_game.pl2_score" type="number" />
        </label>

        <div>
          <button @click="closeModal"></button>
          <button @click="submitScore"></button>
          <button @click="finalizeMatch"> </button>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
  import Modal from './Modal.vue';

  export default {
    name: "Game",
    components: {
      Modal
    },
    props: [
      'game',
      'hover-player',
      'hovered-player',
      'unhover-player',
      'set-match-score',
      'make-match-done'
    ],
    updated: function () {
      if (!this.editingMatch) {
        this._game = {...this.game};
      }
    },
    data: function () {
      return {
        editingMatch: false,
        _game: {}
      };
    },
    methods: {
      openModal() {
        this.editingMatch = true;
      },
      closeModal() {
        this.editingMatch = false;
      },
      submitScore() {
        this.setMatchScore(this._game.id, Number(this._game.pl1_score), Number(this._game.pl2_score));
        this.$nextTick(this.closeModal);
      },
      finalizeMatch() {
        this.setMatchScore(this._game.id, Number(this._game.pl1_score), Number(this._game.pl2_score))
          .then(() => {
            if (confirm(' ?    !')) {
              this.makeMatchDone(this.game.id);
              this.$nextTick(this.closeModal);
            }
          });
      },
      getCN(isFirst) {
        const pl1_score = Number(this.game.pl1_score);
        const pl2_score = Number(this.game.pl2_score);

        if (pl1_score === pl2_score) {
          return '';
        } else if (pl1_score > pl2_score) {
          return isFirst ? 'winner' : 'loser';
        } else {
          return isFirst ? 'loser' : 'winner';
        }
      }
    }
  }
</script>

<style>
  .match {
    position: relative;
    border-radius: 2px;
    border: 1px gray solid;
    margin: 4px 0;
    background: #fff;
    z-index: 1;
    transition: all .3s;
  }

  .player-row {
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    cursor: pointer;
    background: rgba(0,0,0,0.05);
    transition: .15s background ease-in-out;
    white-space: nowrap;
  }

  .player-row:first-child {
    border-bottom: 1px gray solid;
  }

  .hovered-player {
    animation: glow 3s ease-in infinite both;
  }

  .hovered-player .player-name {
    background: rgba(0,0,255,0.1);
  }

  .winner.hovered-player .score {
    background: rgba(0,255,0,0.3);
  }

  .hovered-player.loser .score {
    background: rgba(255,0,0,0.3);
  }

  .hovered-player:not(.winner):not(.loser) .score {
    background: #fff;
  }

  .player-name, .score {
    padding: 4px 7px;
    display: block;
    transition: .15s background ease-in-out;
  }

  .player-name {
    flex-grow: 1;
  }

  .score {
    flex-grow: 0;
    border-left: 1px gray solid;
  }

  .winner .score,
  .winner .player-name {
    font-weight: bold;
  }

  .modal-content {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>



